import React, {useState, useEffect, Fragment} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button,
    MiniInfoCell,
    Caption,
    Subhead,
    TabsItem,
    Tabs,
    Div,
    ActionSheet,
    ActionSheetItem,
    Avatar,
    Input,
    RichCell,
    Footer, Separator, Counter, Group
} from "@vkontakte/vkui";
import $ from 'jquery'
import './Home.css'
import bridge from '@vkontakte/vk-bridge';
import fetchJsonp from "fetch-jsonp"
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon20WorkOutline from '@vkontakte/icons/dist/20/work_outline';
import Icon56ArticleOutline from '@vkontakte/icons/dist/56/article_outline';

const axios = require('axios');
const jsonpAdapter = require('axios-jsonp');

const Cart = ({ id, go, fetchedUser, activeItem, onDecrementPosition, onIncrementPosition, order, clearCart, onDeletePosition, setPopout }) => {
    const [sum, setSum] = useState(null);
    const [count, setCount] = useState(null);
    const [sumDeliv, setSumDeliv] = useState(null);
    const [weight, setWeight] = useState(null);
    const [postal, setPostal] = useState(null);
    const [qCity, setQCity] = useState(null);
    const [activeCity, setActiveCity] = useState(null);
    const [deliv, setDeliv] = useState(null);

    useEffect(() => {
        if (activeCity !== null) getRange(activeCity.id)
    }, [activeCity]);

    useEffect(()=>{
        getOrder(order)
        console.log(order)
    }, [order]);


    useEffect(() => {
        getOrder(order)
    }, []);
    const getOrder = (order) => {
        let count = 0;
        let sum = 0;
        let weight = 0;
        for (let key in order){
            count = count + order[key].count;
            sum = sum + order[key].count * order[key].item.price
            weight = weight + order[key].count * order[key].item.weight
        }
        setCount(count);
        setSum(sum);
        setWeight(weight);
    }

    const getRange = (cityId) => {
        //sahpossum.herokuapp.com
        //localhost:3000
        fetch('https://sahpossum.herokuapp.com/items/getRange', {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify({
                "cityCode": cityId,
                "weight": String(weight),
                "count": count
            }), // тип данны
        })
            .then(res=>res.json())
            .then(res=>{
                console.log(res)
                if (res.error) {
                    setDeliv(<MiniInfoCell textWrap={'full'}>{res.error[0].text}</MiniInfoCell>)
                } else {
                    setDeliv(<Fragment>
                        <MiniInfoCell after={Number(res.result.price)}>Стоимость доставки</MiniInfoCell>
                        <MiniInfoCell after={res.result.deliveryDateMax}>Доставлено до:</MiniInfoCell>
                        <MiniInfoCell after={Number(res.result.price) + Number(sum)}>Итого к оплате: </MiniInfoCell>
                    </Fragment>)
                }
            })
            .catch(e=>{
                console.log(e)
            })
    }

    const pay = (fetchedUser) => {
        if (fetchedUser.id){
            bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
                    // "data": {},
                    "amount": 1,
                    "user_id": 199500866,
                    // "group_id": 199500866,
                    "description": `Оплата товаров на сумму ${sum} в магазине (в рамках тестирования сумма 1 рубль)`
                },
            });
        }
    }

    const getAddress = () => {
        $.ajax({
            url : "https://api.cdek.ru/city/getListByTerm/jsonp.php?callback=?",
            dataType : "jsonp",
            data : {
                q : qCity,
                name_startsWith : qCity
            },
            success : function(data) {
                console.log(data.geonames)
                let arrCities = []
                   data.geonames.map(city=>{
                       arrCities.push(
                           <ActionSheetItem onClick={()=>setActiveCity(city)} autoclose>
                               {city.name}
                           </ActionSheetItem>
                       )
                   })
                setPopout(
                    <ActionSheet onClose={() => {
                        setPopout(null)
                    }}>
                        {arrCities}
                        {/*{osname === IOS && <ActionSheetItem autoclose mode="cancel">Отменить</ActionSheetItem>}*/}
                    </ActionSheet>
                )
            }
        });

    }

    const renderOrderList = (order) => {
        let arr = [];
        for (let key in order) {
            arr.push(
                <React.Fragment>
                    <RichCell
                        before={<Avatar size={92} src={order[key].item.img}/>}
                        text={
                            <MiniInfoCell after={<Button onClick={()=>onDeletePosition(order[key].item)} style={{color: 'red'}}>Удалить</Button>}>
                                <button
                                    className="Place__food-button"
                                    onClick={() => {
                                        onDecrementPosition(order[key].item);
                                    }}
                                >
                                    -
                                </button>
                                <span>&nbsp;{key in order ? order[key].count : 0}&nbsp;</span>
                                <button
                                    className="Place__food-button"
                                    onClick={() => {
                                        onIncrementPosition(order[key].item);
                                    }}
                                >
                                    +
                                </button>
                            </MiniInfoCell>
                        }
                        caption={<MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{order[key].item.price} руб.</Subhead></MiniInfoCell>}
                    >
                        <MiniInfoCell textWrap={'full'}>{order[key].item.name}</MiniInfoCell>
                    </RichCell>
                    <Separator/>
                </React.Fragment>
            )
        }
        if (arr.length !== 0) arr.push(<Button onClick={()=>clearCart()}>Очистить корзину</Button>)
        return arr
    }
    if (count === 0){
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
                <Placeholder
                    stretched
                    icon={<Icon56ArticleOutline />}
                    header={'Ваша корзина пуста'}
                    action={<Button onClick={()=>go('home')} mode={'outline'} size="l">Продолжить покупки</Button>}
                />
                <Footer/>
            </Panel>
        )
    } else {
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
                {renderOrderList(order)}
                <Fragment>
                    <Div>
                        <MiniInfoCell after={<Counter>{count}</Counter>}>Товаров (штуки)</MiniInfoCell>
                        <MiniInfoCell after={<Counter>{sum}</Counter>}>Сумма (рубли)</MiniInfoCell>
                    </Div>
                    {
                        activeCity === null ?
                            <Div>
                                <Group header={<Header mode="secondary">Укажите город доставки</Header>}>
                                    <Input value={qCity} onChange={(e)=>setQCity(e.target.value)} />
                                    {
                                        qCity !== null && <Button mode={'outline'} size={'xl'}  onClick={()=>getAddress()}>Рассчитать доставку</Button>
                                    }
                                </Group>
                            </Div> :
                            <Div>
                                <MiniInfoCell textWrap={'full'} mode={'more'}>{activeCity.name}</MiniInfoCell>
                                {deliv || ''}
                            </Div>
                    }
                    <Div>
                        <Button mode={'outline'} size={'xl'}  onClick={()=>pay(fetchedUser)}>Оплатить</Button>
                    </Div>
                </Fragment>
                <Footer/>
            </Panel>
        )
    }
};

export default Cart
